	goBack = () => {
		let panel = this.state.activePanel;
		let modal = this.state.activeModal;
		if(panel==='main' && modal===null){
			if(osname === ANDROID_CLIENT){
				window.android_client.send("exit", {});
			}else{
				bridge.send("VKWebAppClose", {"status": "success"});
			}
		}else if (panel==='error') {
			if(osname === ANDROID_CLIENT){
				window.android_client.send("exit", {});
			}else{
				bridge.send("VKWebAppClose", {"status": "success"});
			}
		}else {
			if(panel==='game'){
				this.exitGame(cur_game_id);
			}
			let history = this.state.history;
			let modalHistory = this.state.modalHistory;
			if(modalHistory.length===0){
				if(history.length === 1) {
					if(osname === ANDROID_CLIENT){
						window.android_client.send("exit", {});
					}else{
						bridge.send("VKWebAppClose", {"status": "success"});
					}
				} else if (history.length > 1) {
					history.pop();
					let p = history[history.length - 1];
					this.setState({activePanel: p});
				}
			}else{
				this.modalBack();
			}
		}
	};

	goToPage(page) {
		var lpage = this.state.activePanel;
		this.state.history.push(page);
		window.history.pushState({panel: page}, 'Title');
		this.setState({activePanel: page, activeModal: null, modalHistory: [], popout: null});
		bridge.send("VKWebAppSetLocation", {"location": "panel_"+page});
		if(lpage==='game' && page==='main'){
			this.setState({popout: <ScreenSpinner />});
			setTimeout(()=>{
				this.setState({popout: null});
			},500);
		}
	}

	setActiveModal(activeModal) {
		activeModal = activeModal || null;
		let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

		if (activeModal === null) {
			modalHistory = [];
		} else if (modalHistory.indexOf(activeModal) !== -1) {
			modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
		} else {
			modalHistory.push(activeModal);
		}

		window.history.pushState({modal: activeModal}, 'Title');

		this.setState({activeModal: activeModal, modalHistory: modalHistory});
		if(modalHistory.length===0){
			bridge.send("VKWebAppSetLocation", {"location": "panel_"+this.state.activePanel});
		}else{
			bridge.send("VKWebAppSetLocation", {"location": "modal_"+activeModal});
		}
		if(modalHistory.length>0){
			this.setState({popout: <ScreenSpinner />});
			setTimeout(()=>{
				this.setState({popout: null});
			},450);
		}
	};

