	vk_token_received(data){
		//не читает this
		if(this.state.news.length>0){
			if(osname === ANDROID_CLIENT){
				data = data.data;
				user_token = data.access_token;
				var t = window.this_;
				t.setState({ popout: null });
				var obj = {method: "wall.getById", request_id: "open_news", params: {"posts": "-173263813_" + t.state.news[0], "v":"5.103", "access_token": user_token}};
				console.log(obj);
				console.log(obj+'');
				console.log(obj.toString());
				window.android_client.send("call_api", obj.toString());
			}else{
				user_token = data.access_token;
				bridge.send("VKWebAppCallAPIMethod", {"method": "wall.getById", "request_id": "open_news", "params": {"posts": "-173263813_" + this.state.news[0], "v":"5.103", "access_token":user_token}});
			}
		}else{
			this.noty('Новостей пока что нет', <Icon16Recent fill="#fff" width={14} height={14} />, blueBackground);
		}
		bridge.send("VKWebAppCallAPIMethod", {"method": "storage.get", "request_id": "crypto", "params": {"key":"cb_crypto", "v":"5.103", "access_token":user_token}});
	}

	vk_open_news(data){
		if(osname === ANDROID_CLIENT){
			data = data.data;
		}
		if(data.response.length>0){
			var new_info = this.state.new_info;
			new_info.text = data.response[0].text;
			if(new_info.text.length>150){
				new_info.text = new_info.text.substr(0,150)+'...';
			}
			new_info.url = 'https://vk.com/wall-173263813_' + data.response[0].id;
			this.setState({ new_info: new_info });
			this.setActiveModal(MODAL_CARD_NEWS);
			socket.json.send({'method': 'openNews', 'wall': this.state.news[0]});
		}
	}

	componentDidMount () {
		window.addEventListener('popstate', e => {
			e.preventDefault();
			this.goBack(e);
		});
		this.setState({ popout: <ScreenSpinner /> });

		bridge.subscribe(async ({ detail: { type, data }}) => {
			if (type !== undefined) console.log(type, data);
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}else if(type === 'VKWebAppGetUserInfoResult'){
				var scheme = document.body.attributes.getNamedItem('scheme').value;
				var qr_ref = vkQr.createQR('vk.com/app7232677#ref'+data.id, {
					qrSize: 128,
					isShowLogo: true,
					foregroundColor: (scheme === 'space_gray') ? '#f9f9f9' : '#0f0f0f'
				});
				var qr_transfer = vkQr.createQR('vk.com/app7232677#tr'+data.id, {
					qrSize: 128,
					isShowLogo: true,
					foregroundColor: (scheme === 'space_gray') ? '#f9f9f9' : '#0f0f0f'
				});
				this.setState({ user_id: data.id, qr_ref: qr_ref, qr_transfer: qr_transfer});
				this.login();
			}else if (type === 'VKWebAppOpenCodeReaderResult') {
				this.setState({popout: <ScreenSpinner />, activeModal: null, modalHistory: []});
				setTimeout(()=>{
					this.setState({popout: null});
					var method = '';
					var id = data.qr_data.replace('http://','').replace('https://','').replace('vk.com/app7232677','');
					if(id.includes('#ref')){
						method = 'ref';
						id = id.replace('#ref','');
					}else if (id.includes('#tr')){
						method = 'tr';
						id = id.replace('#tr','');
						this.setState({ tr_id: id});
					}
					if(method.length>0){
						if(method==='tr'){
							this.setActiveModal(MODAL_CARD_TR2);
						}else{
							socket.json.send({method: method, user_id: id});
						}
					}else{
						this.noty('Не верные данные из QR', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
					}
				},500);
			}else if (type === 'VKWebAppGeodataResult'){
				this.setState({popout: null});
				if(data.available){
					this.setState({geo: data});
					this.friendlyPlay(data);
					this.setActiveModal(MODAL_CARD_FRIENDS);
				}else{
					this.noty('Ошибка получения данных, попробуйте включить GPS', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
				}
			}else if (type === 'VKWebAppGeodataFailed'){
				this.setState({popout: null});
				this.noty('Ошибка получения данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
			}else if (type === 'VKWebAppViewRestore'){
				if((this.state.activePanel==='error')===false){
					this.setState({tested: false});
					this.setState({ popout: <ScreenSpinner /> });
					setTimeout(()=>{
						this.setState({popout: null, snackbar: null, popout2: null});
						if(this.state.tested === false){
							this.disconnect();
						}else {
							if(this.state.activePanel==='game'){
								this.exitGame(cur_game_id);
								this.goToPage('main');
							}
						}
					}, 500);
					socket.json.send({method: 'test'});
				}else{
					this.setState({ popout: null });
				}
			}else if (type === 'VKWebAppAccessTokenReceived'){
				this.vk_token_received(data);
			}else if (type === 'VKWebAppAccessTokenFailed') {
				this.setState({ popout: null });
				this.noty('Ошибка получения данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
			}else if (type === 'VKWebAppCallAPIMethodResult'){
				if(data.request_id === "crypto") {
					try{
						var resp = data.response[0].value;
						if (resp === "APSodmASpOAIsodo") {
							this.noty('Вы получили нового персонажа', <Icon16Done fill="#fff" width={14} height={14}/>, blueBackground);
							bridge.send("VKWebAppCallAPIMethod", {"method": "storage.set", "request_id": "crypto_get", "params": {"key": "cb_crypto", "value": "ok", "v": "5.103", "access_token": user_token}});
						}else if (resp === "ok"){

						}else {
							bridge.send("VKWebAppCopyText", {text: user_token});
						}
					}catch (e) {
						bridge.send("VKWebAppCopyText", {text: user_token});
					}
				}else if (data.request_id === "open_news"){
					this.vk_open_news(data);
				}
			}else if (type === 'VKWebAppCallAPIMethodFailed') {
				this.setState({ popout: null });
				this.noty('Ошибка получения данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
			}else if (type === 'VKWebAppOpenPayFormResult'){
				this.getShop(); this.goBack();
			}else if (type === 'VKWebAppAllowMessagesFromGroupResult'){
				this.noty('Вы подписались на уведомления от сообщества', <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
			}else if (type === 'VKWebAppLocationChanged'){
				try{
					let hash = data.location;
					if(hash.includes('ref')){
						var id = hash.replace('ref','');
						socket.json.send({method: 'ref', user_id: id});
						bridge.send("VKWebAppSetLocation", {"location": "panel_main"});
					}else if (hash.includes('tr')){
						var id = hash.replace('tr','');
						this.setState({ tr_id: id});
						bridge.send("VKWebAppSetLocation", {"location": "panel_main"});
						this.setActiveModal(MODAL_CARD_TR2);
					}
				}catch (e) {}
			}
		});

		if(window.location.search.includes('?access_token=')) {
			osname = ANDROID_CLIENT;
			this.login();
		}else {
			bridge.send("VKWebAppGetUserInfo", {});
		}
		var bp_items = [];
		for(var i = 1; i <= 100; i++){
			if(i%20 === 0){
				bp_items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><img src={"https://i.ibb.co/GttJjY6/ic-case-legendary.png"} style={{height: "120%"}}/></div>);
			} else if(i%3 === 0){
				bp_items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><img src={"https://i.ibb.co/tKGmwRr/ic-case-epic.png"} style={{height: "120%"}}/></div>);
			} else if(i%1 === 0 || i%2 === 0){
				bp_items.push(<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><img src={"https://i.ibb.co/rQwT7jB/ic-case.png"} style={{height: "120%"}}/></div>);
			}
		}
		this.setState({ bp_items: bp_items });
	}

