import React, { PureComponent } from 'react';
import '../css/MainButtons.css';

export class MainButton extends PureComponent {

    render() {
        return (
            <div className={'btn'}>
                <div style={{top: "50%", left: "50%", transform: "translate(-50%, -50%)", position: "absolute"}}>{this.props.icon}</div>
            </div>
        )
    }

}